.slot {
    
    font-size: 8px;
    border:1px solid black;
    min-width: 16px;
    max-width: 16px;
    min-height: 16px;
    max-height: 16px;
    overflow: hidden;


}
.slot-undefined {background-color: grey;

}
.columnn-room {
    min-width: 200px;
    flex-grow: 1;
}
.displaytitle{
    font-family: Arial, Helvetica, sans-serif;
    padding: 16px;
    font-size: 48px;
  text-transform: uppercase;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.room-available{
    background-color: #34a940;
    font-family: Arial, Helvetica, sans-serif;
    padding: 16px;
    
    text-align: left !important;
    font-size: 18px !important;
color:#ffffff;
  overflow: hidden;
  border-radius: 10px;
}

.room-available button {
    background-color: transparent;
    color:#ffffff;
    border: 1px solid white;
    padding: 8px;
}
.room-buzy{
    background-color: #9d2524;
    color:#ffffff;
}

.room-soonbuzy{
    background-color: #34a940;
}
.status-block{
    padding-top: 12px;
    padding-bottom: 32px;
}
.status-heading{
    font-size: 32px;
    line-height: 32px;
  
}

.status-subheading{
    
    padding-top: 4px;
    font-size: 18px;
}


.clock {
    font-family: Arial, Helvetica, sans-serif;

    text-align: right;
    position: fixed;
    right: 0;
    bottom: 0;
}


.clock-hm {

font-size: 32px;
    
}

.next{
    font-family: Arial, Helvetica, sans-serif;

    margin-top: 20px;
    margin-left: 16px;
    
  
}
.nextHeader{
    margin-top: 20px;
    font-size: 32px;
    line-height: 32px;
  
}